
.stars {
  display: flex;
  flex-direction: row;
  font-family: "Poppins", sans-serif;
  &__star {
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
  }

  &__star:not(:first-child) {
    margin-left: 4px;
  }

  &--1 :nth-child(-n+1),
  &--2 :nth-child(-n+2),
  &--3 :nth-child(-n+3),
  &--4 :nth-child(-n+4),
  &--5 :nth-child(-n+5) {
    background-image: url(../images/star-active.svg);
  }

}
